@import url("https://fonts.googleapis.com/css2?family=Bungee+Shade&display=swap");

.card {
  text-align: center;
  padding: 40px;
  background-image: linear-gradient(134.59deg, #373749 3.19%, #181820 105.75%);
  border-radius: 5px;
  color: #f9f9fa;

  .number {
    font-family: "Bungee Shade", cursive;
    font-size: 70px;
    margin-bottom: 18px;
    line-height: 1em;
    color: #c7c7d1;
  }
}
